<template>
  <mui-page title="Content Type : Relation">
    <mui-frame>
      <div slot="header">
        <el-button type="primary" size="mini" @click="dialog.header.visible = true"><i class="fa fa-edit"></i> Edit</el-button>
        <div class="frame-title"></div>
      </div>
      <div slot="body">
        <div class="prop">
          <div class="prop-key">Name</div>
          <div class="prop-val">John Doe</div>
        </div>
        <div class="prop">
          <div class="prop-key">Gender</div>
          <div class="prop-val">Male</div>
        </div>
        <div class="prop">
          <div class="prop-key">Age</div>
          <div class="prop-val">Top Secret !! (^-^)</div>
        </div>
        <div class="prop">
          <div class="prop-key">Residential Address</div>
          <div class="prop-val">Kagurazaka, Shinjuku-ku, Tokyo, JP</div>
        </div>
        <div class="prop">
          <div class="prop-key">E-Mail Address</div>
          <div class="prop-val">john.doe@monoplus.jp</div>
        </div>
      </div>
    </mui-frame>

    <mui-frame title="Career Background">
      <div slot="header">
        <el-button type="primary" size="mini" @click="dialog.frame[0].visible = true"><i class="fa fa-plus"></i> Add</el-button>
      </div>
      <div slot="body">
        <el-table :data="tableData4" style="width: 100%" max-height="250">
          <el-table-column fixed prop="date" label="Date" width="150"></el-table-column>
          <el-table-column prop="name" label="Name" width="120"></el-table-column>
          <el-table-column prop="state" label="State" width="120"></el-table-column>
          <el-table-column prop="city" label="City" width="120"></el-table-column>
          <el-table-column prop="address" label="Address" width="300"></el-table-column>
          <el-table-column prop="zip" label="Zip" width="120"></el-table-column>
          <el-table-column fixed="right" label="Operations" width="120">
            <template slot-scope="scope">
              <el-button type="text" size="mini"
                @click="dialog.frame[0].visible = true">
                Edit
              </el-button>
            </template>
          </el-table-column>
        </el-table>
      </div>
    </mui-frame>

    <mui-frame title="Educational Background">
      <hot-table slot="body" :root="hot.root" :settings="hot.conf" :data="hot.data"></hot-table>
    </mui-frame>

    <mui-frame title="Licenses">
      <hot-table slot="body" :root="hot.root" :settings="hot.conf" :data="hot.data"></hot-table>
    </mui-frame>

    <div class="page-spacer"></div>

    <el-dialog title="Edit Header" :visible.sync="dialog.header.visible">
    </el-dialog>

    <el-dialog title="Edit Frame 1" :visible.sync="dialog.frame[0].visible">
    </el-dialog>

    <el-dialog title="Edit Frame 2" :visible.sync="dialog.frame[1].visible">
    </el-dialog>

  </mui-page>
</template>

<script>
import hotData from './hotData.json'

export default {
  data () {
    return {
      meta: null,
      dialog: {
        add: {
          fileList: [],
          visible: false
        },
        header: {visible: false},
        frame: [
          {visible: false},
          {visible: false}
        ],
        autocomplete: {visible: false},
        search: {visible: false}
      },
      hot: {
        root: 'hot0',
        conf: {
          width: '100%',
          height: 156,
          wordWrap: false,
          visibleRows: 10,
          fixedColumnsLeft: 2,
          columnSorting: true,
          manualColumnResize: true,
          manualColumnMove: true,
          maxRows: 5,
          rowHeaders: true,
          colHeaders: [],
          columns: [
            {data: '_id', type: 'text'},
            {data: 'index', type: 'numeric'},
            {data: 'guid', type: 'text'},
            {data: 'isActive', type: 'checkbox'},
            {data: 'balance', type: 'text'},
            {data: 'picture', type: 'text'},
            {data: 'age', type: 'numeric'},
            {data: 'eyeColor', type: 'text'},
            {data: 'name', type: 'text'},
            {data: 'gender', type: 'text'},
            {data: 'company', type: 'text'},
            {data: 'email', type: 'text'},
            {data: 'phone', type: 'text'},
            {data: 'address', type: 'text'},
            {data: 'about', type: 'text'},
            {data: 'registered', type: 'text'},
            {data: 'latitude', type: 'numeric'},
            {data: 'longitude', type: 'numeric'}
          ]
          // minCols: 26,
          // width: 1024,
        },
        data: hotData.data,
        pagination: {
          index: 1
        }
      },
      tableData4: [{
        date: '2016-05-03',
        name: 'Tom',
        state: 'California',
        city: 'Los Angeles',
        address: 'No. 189, Grove St, Los Angeles',
        zip: 'CA 90036'
      }, {
        date: '2016-05-02',
        name: 'Tom',
        state: 'California',
        city: 'Los Angeles',
        address: 'No. 189, Grove St, Los Angeles',
        zip: 'CA 90036'
      }],
      form: null
    }
  },
  methods: {
    toggle (event) {
      event.target.parentNode.parentNode.parentNode.parentNode.classList.toggle('collapse')
    },
    handleSizeChange (val) {
      console.log(`${val} items per page`)
    },
    handleCurrentChange (val) {
      console.log(`current page: ${val}`)
    }
  }
}
</script>



