<template>
  <div class="page split">

    <div class="page-left">
      <div class="page-header">
      </div>
      <div class="page-body">
        <el-button type="primary">Filter Action</el-button>
        <div class="prop">
          <div class="prop-key">Option 1</div>
          <div class="prop-val">
            <el-checkbox>Option</el-checkbox><br>
            <el-checkbox>Option</el-checkbox><br>
            <el-checkbox>Option</el-checkbox><br>
            <el-checkbox>Option</el-checkbox><br>
            <el-checkbox>Option</el-checkbox><br>
            <el-checkbox>Option</el-checkbox><br>
          </div>
        </div>
        <div class="prop">
          <div class="prop-key">Option 3</div>
          <div class="prop-val">
            <el-select v-model="optVal" multiple placeholder="Select">
              <el-option v-for="item in options" :key="item.value"
                :label="item.label" :value="item.value">
              </el-option>
            </el-select>
          </div>
        </div>
        <div class="prop">
          <div class="prop-key">Option 2</div>
          <div class="prop-val">
            <el-checkbox>Option</el-checkbox><br>
            <el-checkbox>Option</el-checkbox><br>
            <el-checkbox>Option</el-checkbox><br>
            <el-checkbox>Option</el-checkbox><br>
            <el-checkbox>Option</el-checkbox><br>
            <el-checkbox>Option</el-checkbox><br>
          </div>
        </div>
      </div>
    </div>

    <div class="page-main">
      <div class="page-header">
        <el-button type="primary" onclick="history.back()">&laquo; 戻る</el-button>
        <div class="page-title">Content Type : Facet</div>
        <el-button type="primary"><i class="fa fa-cogs"></i> 設定</el-button>
      </div>
      <div class="page-body" style="display: flex; flex-wrap: wrap;">

        <div class="item" v-for="(c, i) in data" :key="i" style="margin: 0 16px 16px 0; min-width: 196px; background-color: #fff;">
          <div class="item-sub">{{c.company}}</div>
          <div class="item-title">{{c.name}}</div>
          <span class="item-desc">{{c.age}} yo</span> /
          <span class="item-desc">{{c.gender}}</span> /
          <span class="item-desc">eye: </span>
          <span class="item-desc" :style="{color:c.eyeColor}">{{c.eyeColor}}</span>
        </div>

      </div>

      <div class="page-spacer"></div>

    </div>
  </div>
</template>

<script>
import hotData from './hotData.json'

export default {
  data () {
    return {
      meta: null,
      dialog: {
        add: {
          fileList: [],
          visible: false
        },
        edit: {visible: false},
        columns: {visible: false},
        autocomplete: {visible: false},
        search: {visible: false}
      },
      data: hotData.data,
      options: [
        {label: 'Option Label 1', value: 'Value 1'},
        {label: 'Option Label 2', value: 'Value 2'},
        {label: 'Option Label 3', value: 'Value 3'},
        {label: 'Option Label 4', value: 'Value 4'},
        {label: 'Option Label 5', value: 'Value 5'},
        {label: 'Option Label 6', value: 'Value 6'}
      ],
      optVal: '',
      form: null
    }
  },
  methods: {
    toggle (event) {
      event.target.parentNode.parentNode.parentNode.parentNode.classList.toggle('collapse')
    },
    handleSizeChange (val) {
      console.log(`${val} items per page`)
    },
    handleCurrentChange (val) {
      console.log(`current page: ${val}`)
    }
  }
}
</script>



