<template>
  <button class="mui-button" @click="click">
    <slot></slot>
  </button>
</template>

<script>
export default {
  name: 'MuiButton',
  props: {
    text: {
      type: String,
      default: '',
      required: false
    }
  },
  methods: {
    click (evt) {
      this.$emit('click', evt)
    }
  }
}
</script>

<style>
.mui-button {
  min-width: 32px;
  /*border: solid 1px #ccc;*/
  border-radius: 4px;
  background-color: #eee;
  padding: 0 12px;
  height: 32px;
  line-height: 32px;
  margin-right: 8px;
  color: #333;
  white-space: nowrap;
}
.mui-button:hover,
.mui-button:focus {
  background-color: #ddd;
  /*border: solid 1px #00f;*/
  color: #000;
}
</style>



