<template>
  <div>
    <div class="mui-header">
      <div class="mui-logo">+ Data Modeler +</div>
      <div></div>
    </div>
    <div class="mui-body">
      <div class="mui-left">
        <div class="mui-toolbar">
          models
        </div>
        <div class="mui-scroll">
          <ul>
            <li class="mui-tree-node" v-for="(m, i) in models" :key="i" :style="{color: (selectedIndex === i ? '#00f' : '')}">
              <div class="mui-tree-label" @click="select(i)">{{m.id}}</div>
            </li>
          </ul>
        </div>
      </div>

      <div class="mui-splitter"></div>

      <div class="mui-main">
        <div class="mui-toolbar">
          <button @click="save">Save</button>
        </div>
        <div class="mui-scroll" v-if="selectedIndex >= 0">
          <div>{{models[selectedIndex].id}} - {{models[selectedIndex].text}}</div>
          <div>Relations</div>
          <pre>{{ json }}</pre>
          <div>Properties</div>
          <hot-table ref="tbl" :root="hot.root" :settings="hot.settings" :data="hot.data"></hot-table>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import models from './data/models.json'
export default {
  data () {
    return {
      hot: {
        root: 'root',
        data: [],
        settings: {
          rowHeaders: true,
          minSpareRows: 1,
          manualColumnResize: true,
          columns: [
            {title: 'id', data: 'id', type: 'text'},
            {title: 'text', data: 'text', type: 'text'},
            {title: 'type', data: 'type', width: 80, editor: 'select', selectOptions: ['string', 'numeric', 'boolean', 'datetime']},
            {title: 'length', data: 'length', type: 'numeric'},
            {title: 'required', data: 'required', type: 'checkbox'},
            {title: 'primary', data: 'primary', type: 'checkbox'},
            {title: 'unique', data: 'unique', type: 'checkbox'},
            {title: 'default', data: 'default', type: 'text'},
            {title: 'regex', data: 'regex', type: 'text'},
            {title: 'ref', data: 'ref', type: 'text'},
            {title: 'comment', data: 'comment', type: 'text'}
          ]
        }
      },
      json: '',
      selectedIndex: -1,
      models
    }
  },
  computed: {
  },
  methods: {
    select (index) {
      this.hot.data = this.models[index].properties
      this.selectedIndex = index
    },
    save () {
      var tbl = this.$refs.tbl.table
      var dat = tbl.getData(0, 0, tbl.countRows() - 2, 10).map(r => ({
        id: r[0] || '',
        text: r[1] || '',
        type: r[2] || '',
        length: r[3] || 0,
        required: r[4] || false,
        primary: r[5] || false,
        unique: r[6] || false,
        default: r[7] || '',
        regex: r[8] || '',
        ref: r[9] || '',
        comment: r[10] || ''
      }))
      console.log('-- dat', dat)
      this.json = JSON.stringify(dat)
      // this.json = JSON.stringify(dat, null, 2)
      console.log('-- json', this.json)
    }
  }
}
</script>



