<template>
  <mui-page title="Content Type : Entry">
    <mui-frame style="text-align: center;">
      <el-upload slot="body" drag class="upload-demo"
        action="https://jsonplaceholder.typicode.com/posts/"
        :on-preview="handlePreview"
        :on-remove="handleRemove"
        :file-list="fileList"
        >
        <i class="el-icon-upload"></i>
        <div class="el-upload__text">Drop file here or <em>click to upload</em></div>
        <div class="el-upload__tip" slot="tip">jpg/png files with a size less than 500kb</div>
      </el-upload>
    </mui-frame>

    <mui-frame title="">
      <div slot="header">
        <el-button type="primary">Back</el-button>
        <el-button type="primary">Sumup</el-button>
        <div style="flex-grow: 1;"></div>
        <el-pagination
          layout="total, sizes, prev, next"
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :current-page.sync="hot.pagination.index"
          :page-sizes="[100, 200, 300, 400]"
          :page-size="100"
          :total="400">
        </el-pagination>
      </div>
      <hot-table slot="body" :root="hot.root" :settings="hot.conf" :data="hot.data"></hot-table>
    </mui-frame>

  </mui-page>
</template>

<script>
import hotData from './hotData.json'

export default {
  data () {
    return {
      meta: null,
      dialog: {
        add: {
          fileList: [],
          visible: false
        },
        edit: {visible: false},
        columns: {visible: false},
        autocomplete: {visible: false},
        search: {visible: false}
      },
      hot: {
        root: 'hot0',
        conf: {
          width: '100%',
          height: 768,
          wordWrap: false,
          visibleRows: 10,
          columnSorting: true,
          manualColumnResize: true,
          manualColumnMove: true,
          minRows: 0,
          rowHeaders: true,
          colHeaders: [],
          columns: [
            {data: '_id', type: 'text'},
            {data: 'index', type: 'numeric'},
            {data: 'guid', type: 'text'},
            {data: 'isActive', type: 'checkbox'},
            {data: 'balance', type: 'text'},
            {data: 'picture', type: 'text'},
            {data: 'age', type: 'numeric'},
            {data: 'eyeColor', type: 'text'},
            {data: 'name', type: 'text'},
            {data: 'gender', type: 'text'},
            {data: 'company', type: 'text'},
            {data: 'email', type: 'text'},
            {data: 'phone', type: 'text'},
            {data: 'address', type: 'text'},
            {data: 'about', type: 'text'},
            {data: 'registered', type: 'text'},
            {data: 'latitude', type: 'numeric'},
            {data: 'longitude', type: 'numeric'}
          ]
          // minCols: 26,
          // width: 1024,
        },
        data: hotData.data,
        pagination: {
          index: 1
        }
      },
      fileList: [],
      form: null
    }
  },
  methods: {
    toggle (event) {
      event.target.parentNode.parentNode.parentNode.parentNode.classList.toggle('collapse')
    },
    handlePreview () {

    },
    handleRemove () {

    },
    handleSizeChange (val) {
      console.log(`${val} items per page`)
    },
    handleCurrentChange (val) {
      console.log(`current page: ${val}`)
    }
  }
}
</script>



