<template>
  <mui-page title="Content Type : Slip">

    <mui-frame title="Header">
      <mui-frame-header slot="header">
        <mui-button>Edit</mui-button>
      </mui-frame-header>
      <div slot="body" style="display: flex; flex-wrap: wrap;">
        <mui-item label="Name" value="John Doe"></mui-item>
        <mui-item label="Gender" value="Male"></mui-item>
        <mui-item label="Age" value="Top Secret !"></mui-item>
        <mui-item label="Residential Address" value="Kagurazaka, Shinjuku-ku, Tokyo, JP"></mui-item>
        <mui-item label="E-Mail Address" value="john.doe@monoplus.jp"></mui-item>
        <mui-item label="Name" value="John Doe"></mui-item>
        <mui-item label="Gender" value="Male"></mui-item>
        <mui-item label="Age" value="Top Secret !"></mui-item>
        <mui-item label="Residential Address" value="Kagurazaka, Shinjuku-ku, Tokyo, JP"></mui-item>
        <mui-item label="E-Mail Address" value="john.doe@monoplus.jp"></mui-item>
      </div>
    </mui-frame>

    <mui-frame title="Detail">
      <mui-frame-header slot="header">
        <el-button type="primary" round>新規</el-button>
        <el-button type="primary" round>編集</el-button>
        <el-button type="primary" round>削除</el-button>
        <div style="flex-grow: 1;"></div>
        <el-pagination
          layout="total, sizes, prev, next"
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :current-page.sync="hot.pagination.index"
          :page-sizes="[100, 200, 300, 400]"
          :page-size="100"
          :total="400">
        </el-pagination>
        <el-button round>設定</el-button>
      </mui-frame-header>
      <hot-table slot="body" :root="hot.root" :settings="hot.conf" :data="hot.data"></hot-table>
    </mui-frame>

  </mui-page>
</template>

<script>
import hotData from './hotData.json'

export default {
  data () {
    return {
      meta: null,
      dialog: {
        add: {
          fileList: [],
          visible: false
        },
        edit: {visible: false},
        columns: {visible: false},
        autocomplete: {visible: false},
        search: {visible: false}
      },
      hot: {
        root: 'hot0',
        conf: {
          width: '100%',
          height: 400,
          wordWrap: false,
          visibleRows: 10,
          fixedColumnsLeft: 2,
          columnSorting: true,
          manualColumnResize: true,
          manualColumnMove: true,
          // maxRows: 5,
          rowHeaders: true,
          colHeaders: [],
          columns: [
            {data: '_id', type: 'text'},
            {data: 'index', type: 'numeric'},
            {data: 'guid', type: 'text'},
            {data: 'isActive', type: 'checkbox'},
            {data: 'balance', type: 'text'},
            {data: 'picture', type: 'text'},
            {data: 'age', type: 'numeric'},
            {data: 'eyeColor', type: 'text'},
            {data: 'name', type: 'text'},
            {data: 'gender', type: 'text'},
            {data: 'company', type: 'text'},
            {data: 'email', type: 'text'},
            {data: 'phone', type: 'text'},
            {data: 'address', type: 'text'},
            {data: 'about', type: 'text'},
            {data: 'registered', type: 'text'},
            {data: 'latitude', type: 'numeric'},
            {data: 'longitude', type: 'numeric'}
          ]
          // minCols: 26,
          // width: 1024,
        },
        data: hotData.data,
        pagination: {
          index: 1
        }
      },
      form: null
    }
  },
  methods: {
    toggle (event) {
      event.target.parentNode.parentNode.parentNode.parentNode.classList.toggle('collapse')
    },
    handleSizeChange (val) {
      console.log(`${val} items per page`)
    },
    handleCurrentChange (val) {
      console.log(`current page: ${val}`)
    }
  }
}
</script>



