import Vue from 'vue'
// import Vuex from 'vuex'
import Router from 'vue-router'
import axios from 'axios'
import HotTable from '@handsontable/vue'
import AceEditor from 'vue2-ace-editor'
import ElementUI from 'element-ui'
import Mui from './mui'

import App from './App'
import Top from './Top.vue'
import Menu from './Menu.vue'

import BuilderTop from './builder/BuilderTop.vue'
import UiBuilder from './builder/UiBuilder.vue'
import DataModeler from './builder/DataModeler.vue'
import SourceCoder from './builder/SourceCoder.vue'
import TextEditor from './builder/TextEditor.vue'
import SampleCrud from './builder/SampleCrud.vue'
import MuiComponents from './Components.vue'

import SampleTop from './sample/SampleTop.vue'
import Single from './sample/Single.vue'
import Slip from './sample/Slip.vue'
import Entry from './sample/Entry.vue'
import SelExec from './sample/SelExec.vue'
import Relation from './sample/Relation.vue'
import Facet from './sample/Facet.vue'
import List from './sample/List.vue'
import Dashboard from './sample/Dashboard.vue'

import 'handsontable/dist/handsontable.full.min.css'
import 'element-ui/lib/theme-chalk/index.css'

import './styles.css'
import './mui/variables.css'

Vue.config.productionTip = false
Vue.use(Router)
Vue.use(ElementUI, {size: 'mini'})
Vue.use(Mui)
Vue.component('hot-table', HotTable)
Vue.component('ace-editor', AceEditor)

axios.defaults.baseURL = 'http://localhost:3000'
Vue.prototype.$http = axios.create({
  baseURL: 'http://localhost:3000'
})

const router = new Router({
  routes: [
    {path: '/', component: Menu},
    {path: '/dev', component: Top},
    {path: '/crud', component: SampleCrud},
    {path: '/components', component: MuiComponents},

    {path: '/builder', component: BuilderTop},
    {path: '/builder/view/*', component: UiBuilder},
    {path: '/builder/data', component: DataModeler},
    {path: '/builder/code', component: SourceCoder},
    {path: '/builder/text', component: TextEditor},

    {path: '/sample', component: SampleTop},
    {path: '/sample/single', component: Single},
    {path: '/sample/slip', component: Slip},
    {path: '/sample/entry', component: Entry},
    {path: '/sample/selexec', component: SelExec},
    {path: '/sample/relation', component: Relation},
    {path: '/sample/facet', component: Facet},
    {path: '/sample/list', component: List},
    {path: '/sample/dashboard', component: Dashboard}
  ]
})

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  components: { App },
  template: '<App/>'
})



