<template>
  <div>
    <div class="mui-header">
      <div class="mui-logo">+ Sample CRUD +</div>
      <div></div>
    </div>
    <div class="mui-body">
      <div class="mui-left">
        <ul>
          <li v-for="table in tables" :key="table"><a href="" @click.prevent="get(table)">{{table}}</a></li>
        </ul>
      </div>
      <div class="mui-main">
        <div class="mui-toolbar">
          <button>Save</button>&nbsp; &nbsp; &nbsp;
          <a href="./#model">Model</a>&nbsp; &nbsp; &nbsp;
          <a href="#model">Index</a>&nbsp; &nbsp; &nbsp;
          <a href="#model">Triger</a>&nbsp; &nbsp; &nbsp;
          <a href="#model">Function</a>
        </div>
        <!-- <div style="padding: 12px;"> -->
          <hot-table :root="hot.root" :settings="hot.settings" :data="hot.data"></hot-table>
        <!-- </div> -->
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data () {
    return {
      tables: [],
      hot: {
        root: 'root',
        data: [{}],
        settings: {
          rowHeaders: true,
          allowInsertRow: true,
          columns: [
          ]
        }
      }
    }
  },
  created () {
    this.$http.get('/api/crud/meta/tables').then(res => {
      this.tables = res.data
    }).catch(err => {
      console.log('-- err', err)
    })
  },
  methods: {
    get (table) {
      this.$http.get('/api/crud/' + table).then(res => {
        this.hot.settings.columns = res.data.fields
        this.hot.data = res.data.rows
      }).catch(err => {
        console.log('-- err', err)
      })
    }
  }
}
</script>



