<template>
  <div class="page split">

    <div class="page-left">
      <div class="page-header">
      </div>
      <div class="page-body">
        <div class="item" v-for="i in [1, 2, 3, 4, 5]" :key="i">
          <div class="item-title">Title</div>
          <div class="item-sub">Subtitle</div>
          <div class="item-desc">Description</div>
        </div>
      </div>
    </div>

    <div class="page-main">
      <div class="page-header">
        <el-button type="primary" onclick="history.back()">&laquo; 戻る</el-button>
        <div class="page-title">Content Type : List</div>
        <el-button type="info"><i class="fa fa-cogs"></i> 設定</el-button>
      </div>
      <div class="page-body">

        <div class="frame">
          <div class="frame-header">
            <div class="frame-title">Header</div>
            <el-button type="text" size="mini" @click="toggle"><i class="fa fa-chevron-down"></i></el-button>
          </div>
          <div class="frame-body pad">
            <div class="prop">
              <div class="prop-key">Name</div>
              <div class="prop-val">John Doe</div>
            </div>
            <div class="prop">
              <div class="prop-key">Gender</div>
              <div class="prop-val">Male</div>
            </div>
            <div class="prop">
              <div class="prop-key">Age</div>
              <div class="prop-val">Top Secret !! (^-^)</div>
            </div>
            <div class="prop">
              <div class="prop-key">Residential Address</div>
              <div class="prop-val">Kagurazaka, Shinjuku-ku, Tokyo, JP</div>
            </div>
            <div class="prop">
              <div class="prop-key">E-Mail Address</div>
              <div class="prop-val">john.doe@monoplus.jp</div>
            </div>
          </div>
        </div>

        <div class="frame">
          <div class="frame-header">
            <div class="frame-title">Career Background</div>
            <el-button type="text" size="mini" @click="toggle"><i class="fa fa-chevron-down"></i></el-button>
          </div>
          <div class="frame-body">
            <el-table :data="tableData4" style="width: 100%" max-height="250">
              <el-table-column fixed prop="date" label="Date" width="150"></el-table-column>
              <el-table-column prop="name" label="Name" width="120"></el-table-column>
              <el-table-column prop="state" label="State" width="120"></el-table-column>
              <el-table-column prop="city" label="City" width="120"></el-table-column>
              <el-table-column prop="address" label="Address" width="300"></el-table-column>
              <el-table-column prop="zip" label="Zip" width="120"></el-table-column>
              <el-table-column fixed="right" label="Operations" width="120">
                <template slot-scope="scope">
                  <el-button type="text" size="mini"
                    @click.native.prevent="deleteRow(scope.$index, tableData4)">
                    Edit
                  </el-button>
                </template>
              </el-table-column>
            </el-table>
          </div>
        </div>

        <div class="frame">
          <div class="frame-header">
            <div class="frame-title">Educational Background</div>
            <el-button type="text" size="mini" @click="toggle"><i class="fa fa-chevron-down"></i></el-button>
          </div>
          <div class="frame-body">
            <hot-table :root="hot.root" :settings="hot.conf" :data="hot.data"></hot-table>
          </div>
        </div>

        <div class="frame">
          <div class="frame-header">
            <div class="frame-title">Licenses</div>
            <el-button type="text" size="mini" @click="toggle"><i class="fa fa-chevron-down"></i></el-button>
          </div>
          <div class="frame-body">
            <hot-table :root="hot.root" :settings="hot.conf" :data="hot.data"></hot-table>
          </div>
        </div>

        <div class="page-spacer"></div>

      </div>
    </div>
  </div>
</template>

<script>
import hotData from './hotData.json'

export default {
  data () {
    return {
      meta: null,
      dialog: {
        add: {
          fileList: [],
          visible: false
        },
        edit: {visible: false},
        columns: {visible: false},
        autocomplete: {visible: false},
        search: {visible: false}
      },
      hot: {
        root: 'hot0',
        conf: {
          width: '100%',
          height: 156,
          wordWrap: false,
          visibleRows: 10,
          fixedColumnsLeft: 2,
          columnSorting: true,
          manualColumnResize: true,
          manualColumnMove: true,
          maxRows: 5,
          rowHeaders: true,
          colHeaders: [],
          columns: [
            {data: '_id', type: 'text'},
            {data: 'index', type: 'numeric'},
            {data: 'guid', type: 'text'},
            {data: 'isActive', type: 'checkbox'},
            {data: 'balance', type: 'text'},
            {data: 'picture', type: 'text'},
            {data: 'age', type: 'numeric'},
            {data: 'eyeColor', type: 'text'},
            {data: 'name', type: 'text'},
            {data: 'gender', type: 'text'},
            {data: 'company', type: 'text'},
            {data: 'email', type: 'text'},
            {data: 'phone', type: 'text'},
            {data: 'address', type: 'text'},
            {data: 'about', type: 'text'},
            {data: 'registered', type: 'text'},
            {data: 'latitude', type: 'numeric'},
            {data: 'longitude', type: 'numeric'}
          ]
          // minCols: 26,
          // width: 1024,
        },
        data: hotData.data,
        pagination: {
          index: 1
        }
      },
      tableData4: [{
        date: '2016-05-03',
        name: 'Tom',
        state: 'California',
        city: 'Los Angeles',
        address: 'No. 189, Grove St, Los Angeles',
        zip: 'CA 90036'
      }, {
        date: '2016-05-02',
        name: 'Tom',
        state: 'California',
        city: 'Los Angeles',
        address: 'No. 189, Grove St, Los Angeles',
        zip: 'CA 90036'
      }],
      form: null
    }
  },
  methods: {
    toggle (event) {
      event.target.parentNode.parentNode.parentNode.parentNode.classList.toggle('collapse')
    },
    handleSizeChange (val) {
      console.log(`${val} items per page`)
    },
    handleCurrentChange (val) {
      console.log(`current page: ${val}`)
    }
  }
}
</script>



