<template>
  <div style="max-width: 1024px; margin: 0 auto;">
    <mui-page title="App Builder">
      <div class="horizontal-seperator"></div>
      <div style="display: flex;">
        <router-link class="mui-menu-item" to="/builder/view">UI Builder</router-link>
        <router-link class="mui-menu-item" to="/builder/data">Data Modeler</router-link>
        <router-link class="mui-menu-item" to="/builder/code">Source Coder</router-link>
        <div class="spacer"></div>
        <router-link class="mui-menu-item" to="/builder/text">Text Editor</router-link>
      </div>
      <div style="display: flex;">
        <router-link class="mui-menu-item" to="/builder/role">Roles</router-link>
        <router-link class="mui-menu-item" to="/builder/menu">Data Modeler</router-link>
        <router-link class="mui-menu-item" to="/builder/code">Source Coder</router-link>
        <div class="spacer"></div>
        <router-link class="mui-menu-item" to="/builder/text">Text Editor</router-link>
      </div>
      <div class="horizontal-seperator"></div>
      <div style="display: flex; align-items: center; padding: 0 0 12px 0;">
        <div style="font-size: 20px;">Contents</div>
        <div class="spacer16"></div>
        <div><b>Search : </b><input type="search" style="font-size: 14px; border: solid 1px #ddd; border-radius: 16px; line-height: 28px; padding: 0 16px;"></div>
        <div class="spacer16"></div>
        <div class="spacer"><b>Sort :</b> id / text / menu / role</div>
        <div class="spacer"></div>
        <router-link class="mui-button" to="/builder/view/">New</router-link>
      </div>

      <div v-for="(c, i) in contents" :key="i" class="content-row">
        <div style="padding-right: 16px; width: 32px;"># {{ i }}</div>
        <div>
          <div style="font-size: 18px;">
            <span class="mui-type">{{ c.type }}</span> &nbsp;
            {{ c.content }} / {{ c.text }}
          </div>
          <div class="content__memo">{{ c.memo }}</div>
          <!-- <div>{{ c.memo }}</div> -->
        </div>
        <!-- <div class="content__models">models</div> -->
        <!-- <div class="content__events">events</div> -->
        <div class="spacer"></div>
        <router-link class="mui-button" :to="'/builder/view/' + i">Edit</router-link>
        <mui-button>Remove</mui-button>
      </div>
    </mui-page>
  </div>
</template>

<script>
export default {
  data () {
    return {
      contents: [
        // {id: '', text: '', type: '', menu: '', roles: [], memo: '', active: false}
      ]
    }
  },
  created () {
    this.getContents()
  },
  methods: {
    getContents () {
      this.$http.get('/api/db/def_content').then(res => {
        console.log('-- contents', res.data)
        this.contents = res.data
      }).catch(err => {
        console.log('-- error', err)
      })
    }
  }
}
</script>

<style>
.content-row:hover {
  background-color: #f3f3f3;
}
.content-row {
  display: flex;
  align-items: center;
  border-bottom: solid 1px #ddd;
  padding: 8px 0;
}
.content__title {

}
.content__type {

}
.content__memo {
  padding: 8px 0 0 0;
}
.content__models {

}
.content__events {

}
.mui-type {
  border: solid 1px #ddd;
  background-color: #eee;
  border-radius: 4px;
  padding: 2px 4px;
}
</style>



