<template>
  <div class="mui-top">
    <h1>Hello World</h1>
    <a href="#" @click.prevent="logout">LOGOUT</a>
    <hr>
    <div class="mui-card" v-for="(m, i) in menu" :key="i">
      <router-link :to="m.path">{{m.title}}</router-link>
    </div>
  </div>
</template>

<script>
export default {
  data () {
    return {
      menu: [{
        title: 'Mui Components',
        path: '/components'
      }, {
        title: 'App Builder',
        path: '/builder/view'
      }, {
        title: 'Data Modeler',
        path: '/builder/data'
      }, {
        title: 'Source Coder',
        path: '/builder/code'
      }]
    }
  },
  methods: {
    logout () {
      this.$http.post('/api/user/logout', {}).then(res => {
        location.replace('http://localhost:3000/')
      }).catch(err => {
        console.log('-- err', err)
      })
    }
  }
}
</script>



