<template>
  <div class="mui-frame-b">
    <div class="mui-frame-b__header" @mouseover="buttons.visible = true" @mouseleave="buttons.visible = false">
      <span><i class="fa fa-caret-down"></i></span>
      <div class="mui-frame-b__title">{{ title }}</div>
      <slot name="header" v-if="buttons.visible"></slot>
    </div>
    <div class="mui-frame-b__body">
      <slot name="body"></slot>
    </div>
  </div>
</template>

<script>
export default {
  name: 'MuiFrameB',
  props: {
    title: {
      type: String,
      default: '',
      required: false
    },
    collapse: {
      type: Boolean,
      default: false,
      required: false
    },
    type: {
      type: String,
      default: 'flow',
      required: false
    }
  },
  data () {
    return {
      buttons: {
        visible: false
      }
    }
  }
}
</script>

<style>
.mui-frame-b {
  border: solid 1px var(--border);
  border-radius: var(--border-radius);
  background-color: #fff;
  margin-bottom: 12px;
}
.mui-frame-b__header {
  display: flex;
  padding: 6px 8px;
  /*background-color: #f0f0f0;*/
}
.mui-frame-b__header > * {
  padding: 0 4px;
  /*font-weight: 600;*/
}
.mui-frame-b__body {
  border-top: solid 1px #ddd;
  padding: 8px;
  /*overflow: show;*/
}
.mui-frame-b_buttons {
  display: flex;
  align-items: center;
}
.mui-frame-b_buttons > * {
  padding: 0 8px;
  /*color: #c00;*/
  /*color: #090;*/
  color: #36f;
}
</style>



