  <div style="padding: 12px;">
    <div style="text-align: center; font-size: 28px; padding: 12px;">WebForm</div>
    <div style="max-width: 1240px; margin: 0 auto;">
      <div class="horizontal-seperator"></div>
      <div style="display: flex; align-items: center;">
        <div style="padding: 8px;"><img src="/static/user.png" alt="avator" style="width: 32px;"></div>
        <div style="padding: 8px;">Yutaka KANAZAWA</div>
        <div style="padding: 8px;">kanazawa.yutaka@monoplus.jp</div>
        <div style="padding: 8px;">Role: developer</div>
        <div style="flex-grow: 1;"></div>
        <div>
          <router-link class="mui-menu-item" to="/builder">
            <i class="fa fa-pencil-alt"></i>&nbsp; App Builder
          </router-link>
          <mui-button @click="ui.settings.visible = true">
            <i class="fa fa-cogs"></i>&nbsp; Settings
          </mui-button>
          <mui-button @click="logout">
            <i class="fa fa-power-off"></i>&nbsp; Log Out
          </mui-button>
        </div>
      </div>
      <div class="horizontal-seperator"></div>
      <div class="mui-menu">
        <div class="mui-menu__title">Sample Wire</div>
        <router-link class="mui-menu-item" to="/sample/single">Single</router-link>
        <router-link class="mui-menu-item" to="/sample/slip">Slip</router-link>
        <router-link class="mui-menu-item" to="/sample/relation">Relation</router-link>
        <router-link class="mui-menu-item" to="/sample/entry">Entry</router-link>
        <router-link class="mui-menu-item" to="/sample/sel_exec">Sel Exec</router-link>
        <router-link class="mui-menu-item" to="/sample/facet">Facet</router-link>
        <router-link class="mui-menu-item" to="/sample/list">List</router-link>
        <router-link class="mui-menu-item" to="/sample/dashboard">Dashboard</router-link>
        <router-link class="mui-menu-item" to="/components">Sample</router-link>
      </div>
      <div class="horizontal-seperator"></div>
      <div class="mui-menu" v-for="(m, i) in menu" :key="i">
        <div class="mui-menu__title">{{ m.text }}</div>
        <router-link class="mui-menu-item" v-for="(c, j) in m.contents" :key="j" :to="c.text">{{ c.text }}</router-link>
      </div>
    </div>

    <el-dialog title="Settings" :visible.sync="ui.settings.visible" width="600px">
    </el-dialog>
  </div>
</template>

<script>
export default {
  data () {
    return {
      ui: {
        settings: {
          visible: false
        }
      },
      menu: [{
        text: 'menu A',
        contents: [{
          text: 'content A'
        }]
      }]
    }
  },
  methods: {
    logout () {
      console.log('-- logout')
      // this.$message('logout', 'logout')
      // this.$http.post('/api/user/logout', {}).then(res => {
      //   location.replace('http://localhost:3000/')
      // }).catch(err => {
      //   console.log('-- err', err)
      // })
    }
  }
}
</script>



