    </div>
    <div class="mui-body">
      <div class="mui-left">
        left
      </div>
      <div class="mui-main">
        <ace-editor v-model="content" @init="editorInit" lang="javascript" theme="chrome" width="100%" height="100%"></ace-editor>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data () {
    return {
      content: 'function aaa() {\nalert(\'aaaaa\')\n}\n\nclass NavigationClass {\n    constructor () {\n        \n    }\n    \n}\n'
    }
  },
  methods: {
    editorInit () {
      require('brace/ext/language_tools')
      // require('brace/mode/html')
      require('brace/mode/javascript')
    }
  }
}
</script>



