<template>
  <mui-page title="Content Type : Single">
    <mui-frame collapsible title="single">
      <mui-frame-header slot="header">
        <el-button round type="danger" @click="removeBulk">一括削除</el-button>
        <el-button round type="warning" @click="dialog.edit.visible = true">一括編集</el-button>
        <el-button round type="primary" @click="dialog.add.visible = true">一括登録</el-button>
        <el-button round type="primary" @click="dialog.edit.visible = true">新規登録</el-button>
        <el-button round type="info" @click="dialog.search.visible = true">Search</el-button>
        <el-dropdown @command="download">
          <el-button round type="info">Download</el-button>
          <el-dropdown-menu slot="dropdown">
            <el-dropdown-item command="xls">Excel</el-dropdown-item>
            <el-dropdown-item command="csv">CSV</el-dropdown-item>
            <el-dropdown-item command="txt">Text</el-dropdown-item>
          </el-dropdown-menu>
        </el-dropdown>
        <div style="flex-grow: 1;"></div>
        <el-pagination
          layout="total, prev, next"
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :current-page.sync="hot.pagination.index"
          :page-sizes="[100, 200, 300, 400]"
          :page-size="100"
          :total="400">
        </el-pagination>
        <el-button round @click="dialog.columns.visible = true">Columns</el-button>
        <el-button round @click="dialog.autocomplete.visible = true">Autocomplete</el-button>

        <el-dialog title="一括登録" :visible.sync="dialog.add.visible">
          <div>
            <el-button @click="dialog.add.visible = false">Close</el-button>
            <el-button @click="add()">Add</el-button>
          </div>
        </el-dialog>

        <el-dialog title="編集" :visible.sync="dialog.edit.visible">
          <div>
            <el-button @click="dialog.edit.visible = false">Close</el-button>
            <el-button @click="save()">Save</el-button>
          </div>
        </el-dialog>

        <el-dialog title="検索" :visible.sync="dialog.search.visible">
          <div>
            <el-button @click="dialog.search.visible = false">Close</el-button>
            <el-button @click="search()">Search</el-button>
          </div>
        </el-dialog>

        <el-dialog title="列設定" :visible.sync="dialog.columns.visible">
          <div>
            <el-button @click="dialog.columns.visible = false">Close</el-button>
            <el-button @click="saveSettings()">Save</el-button>
          </div>
        </el-dialog>

        <el-dialog title="入力補完設定" :visible.sync="dialog.autocomplete.visible">
          <div>
            <el-button @click="dialog.autocomplete.visible = false">Close</el-button>
            <el-button @click="add()">Save</el-button>
          </div>
        </el-dialog>
      </mui-frame-header>
      <hot-table slot="body" :root="hot.root" :settings="hot.conf" :data="hot.data"></hot-table>
    </mui-frame>

  </mui-page>
</template>

<script>
import hotData from './hotData.json'

export default {
  data () {
    return {
      meta: null,
      dialog: {
        add: {
          fileList: [],
          visible: false
        },
        edit: {visible: false},
        columns: {visible: false},
        autocomplete: {visible: false},
        search: {visible: false}
      },
      hot: {
        root: 'hot0',
        conf: {
          width: '100%',
          height: 640,
          wordWrap: false,
          visibleRows: 10,
          fixedColumnsLeft: 2,
          columnSorting: true,
          manualColumnResize: true,
          manualColumnMove: true,
          minRows: 0,
          // rowHeaders: true,
          colHeaders: [],
          columns: [
            {data: '<input type="checkbox">', type: 'checkbox'},
            {data: '_id', type: 'text', readOnly: true},
            {data: 'index', type: 'numeric', readOnly: true},
            {data: 'guid', type: 'text', readOnly: true},
            {data: 'isActive', type: 'checkbox', readOnly: true},
            {data: 'balance', type: 'text', readOnly: true},
            {data: 'picture', type: 'text'},
            {data: 'age', type: 'numeric'},
            {data: 'eyeColor', type: 'text'},
            {data: 'name', type: 'text'},
            {data: 'gender', type: 'text'},
            {data: 'company', type: 'text'},
            {data: 'email', type: 'text'},
            {data: 'phone', type: 'text'},
            {data: 'address', type: 'text'},
            {data: 'about', type: 'text'},
            {data: 'registered', type: 'text'},
            {data: 'latitude', type: 'numeric'},
            {data: 'longitude', type: 'numeric'}
          ]
          // minCols: 26,
          // width: 1024,
        },
        data: hotData.data,
        pagination: {
          index: 1
        }
      },
      form: null
    }
  },
  created () {
    let captions = this.hot.conf.columns.map(m => m.data)
    console.log('captions', captions)
    this.hot.conf.colHeaders = captions
  },
  methods: {
    add () {

    },
    addBulk () {

    },
    save () {

    },
    saveBulk () {

    },
    remove () {

    },
    removeBulk () {

    },
    filter () {

    },
    loadFilter () {

    },
    saveFilter () {

    },
    download (type) {
      console.log('-- donwload type', type)
    },
    settings (type) {

    },
    validate () {

    },
    paginate (type) {

    },
    handleSizeChange (val) {
      console.log(`${val} items per page`)
    },
    handleCurrentChange (val) {
      console.log(`current page: ${val}`)
    }
  }
}
</script>



