<template>
  <div class="mui-frame" :class="{collapsed: collapse}">
    <div class="mui-frame__header">
      <span v-if="collapsible" @click="collapse = !collapse" class="fa fa-caret-down"></span>
      <div class="mui-frame__title">{{ title }}</div>
      <slot name="inline"></slot>
    </div>
    <div class="mui-frame__body">
      <slot name="header"></slot>
      <slot name="body"></slot>
    </div>
  </div>
</template>

<script>
export default {
  name: 'MuiFrame',
  props: {
    title: {
      type: String,
      default: '',
      required: false
    },
    collapsible: {
      type: Boolean,
      default: false,
      required: false
    },
    collapse: {
      type: Boolean,
      default: false,
      required: false
    },
    type: {
      type: String,
      default: 'flow',
      required: false
    }
  },
  data () {
    return {
      collapsed: this.collapse,
      buttons: {
        visible: false
      }
    }
  },
  methods: {
    toggle (event) {
      console.log('-- toggle', event)
    }
  }
}
</script>

<style>
.mui-frame {
  border: solid 1px var(--border);
  border-radius: var(--border-radius);
  background-color: #fff;
  margin-bottom: 12px;
}
.mui-frame__header {
  display: flex;
  padding: 6px 8px;
  /*background-color: #f0f0f0;*/
}
.mui-frame__header > * {
  padding: 0 4px;
  /*font-weight: 600;*/
}
.mui-frame__body-header {
  display: flex;
  align-items: center;
  padding-bottom: 8px;
}
.mui-frame__body {
  border-top: solid 1px #ddd;
  padding: 8px;
  /*overflow: show;*/
}
.mui-frame_buttons {
  display: flex;
  align-items: center;
}
.mui-frame_buttons > * {
  padding: 0 8px;
  /*color: #c00;*/
  /*color: #090;*/
  color: #36f;
}
</style>



