<template>
  <div class="mui-page">
    <div class="mui-page__header">
      <mui-button v-if="true" onclick="history.back()">&laquo; Back</mui-button>
      <div class="mui-page__title">{{ title }}</div>
    </div>
    <slot></slot>
  </div>
</template>

<script>
export default {
  name: 'MuiPage',
  props: {
    title: {
      type: String,
      default: '',
      required: false
    },
    type: {
      type: String,
      default: 'flow',
      required: false
    }
  }
}
</script>

<style>
.mui-page {
  padding: 0 12px 128px 12px;
}
.mui-page__header {
  padding: 12px 0;
  display: flex;
  align-items: center;
}
.mui-page__header > div {
  /*padding: 0 8px 16px 8px;*/
}
.mui-page__title {
  font-size: 20px;
  font-weight: 800;
}
</style>



