<template>
  <div>
    <div class="mui-header">
      <div class="mui-logo">+ Text Editor +</div>
      <div></div>
    </div>
    <div class="mui-body">
      <div class="mui-left" style="padding: 12px;">
        <mui-button>Save</mui-button>
        <div class="horizontal-seperator"></div>
        <mui-button>Add Locale</mui-button>
        <mui-button>Remove Locale</mui-button>
      </div>
      <div class="mui-main">
        <hot-table :root="hot.root" :settings="hot.settings" :data="hot.data"></hot-table>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data () {
    return {
      hot: {
        root: 'root',
        data: [{}],
        settings: {
          rowHeaders: true,
          // allowInsertRow: true,
          spareRows: 1,
          columns: [
            {title: 'key', data: 'key', type: 'text'},
            {title: 'ja_JP', data: 'ja_JP', type: 'text'},
            {title: 'en_US', data: 'en_US', type: 'text'}
          ]
        }
      }
    }
  },
  created () {
    // this.$http.get('/api/crud/meta/tables').then(res => {
    //   this.tables = res.data
    // }).catch(err => {
    //   console.log('-- err', err)
    // })
  },
  methods: {
    get (table) {
      this.$http.get('/api/crud/' + table).then(res => {
        this.hot.settings.columns = res.data.fields
        this.hot.data = res.data.rows
      }).catch(err => {
        console.log('-- err', err)
      })
    }
  }
}
</script>



