<template>
  <div class="mui-item">
    <div class="mui-lbl">{{label}}</div>
    <div class="mui-val">{{value}}</div>
  </div>
</template>

<script>
export default {
  name: 'MuiItem',
  props: {
    label: String,
    value: String
  },
  data () {
    return {
    }
  },
  methods: {
  }
}
</script>

<style>
.mui-item {
  /*box-sizing: border-box;*/
  min-width: calc(1024px / 4 - 16px);
  /*min-width: calc(1024px / 6 - 16px);*/
  width: 24%;
  /*width: 16%;*/
  padding: 0 4px 12px 4px;
}
.mui-lbl {
  font-size: 14px;
  color: #666;
  line-height: 22px;
}
.mui-val {
  font-size: 16px;
  color: #000;
  line-height: 22px;
}
</style>



